"use client";
import React, { useState, useEffect } from "react";
import { Menu, X, Sun, Moon } from "lucide-react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { useTheme } from "../context/ThemeContext";

const Header = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const { isDark, toggleTheme } = useTheme();

  const pathname = usePathname(); // Get current path

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const navLinks = [
    { name: "Home", href: "/" },
    { name: "About", href: "/about" },
    { name: "Services", href: "/services" },
    { name: "Products", href: "/products" },
    { name: "Portfolio", href: "/portfolio" },
    { name: "Contact", href: "/contact" },
  ];

  return (
    <nav
      className={`fixed w-full z-50 transition-all duration-300 ${
        isScrolled
          ? "backdrop-blur-sm bg-white/30 py-4 "
          : "bg-transparent py-6 text-neutral-900"
      }`}
    >
      <div className="max-w-11/12 mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center">
          <div className="flex items-center space-x-2">
            <div className="w-12 h-12 bg-linear-to-br from-blue-600 to-purple-600 rounded-lg flex items-center justify-center">
              <span className="text-white font-bold text-xl">SI</span>
            </div>
            <div>
              <h1
                className={`font-bold text-xl ${
                  isScrolled ? "text-gray-900 dark:text-white" : "text-white"
                }`}
              >
                Strengths Innovations
              </h1>
              <p
                className={`text-xs ${
                  isScrolled
                    ? "text-gray-600 dark:text-gray-300"
                    : "text-gray-200"
                }`}
              >
                Premium Printing Solutions
              </p>
            </div>
          </div>

          {/* Desktop Navigation */}
          <div className="hidden md:flex space-x-8">
            {navLinks.map((link) => {
              const isActive = pathname === link.href;
              return (
                <Link
                  key={link.name}
                  href={link.href}
                  className={`relative font-medium text-xl transition-colors ${
                    isScrolled
                      ? isActive
                        ? "text-blue-600 dark:text-blue-400"
                        : "text-gray-700 dark:text-gray-300 hover:text-blue-600 dark:hover:text-blue-400"
                      : isActive
                      ? "text-white"
                      : "text-white hover:text-blue-300"
                  }`}
                >
                  {link.name}

                  {/* SVG underline */}
                  <svg
                    className={`absolute left-0 -bottom-1 w-full h-1 transition-all ${
                      isActive
                        ? "stroke-blue-600"
                        : isScrolled
                        ? "stroke-transparent"
                        : "stroke-transparent"
                    }`}
                    viewBox="0 0 100 5"
                    preserveAspectRatio="none"
                  >
                    <path
                      d="M0 2.5 Q25 0 50 2.5 T100 2.5"
                      className="stroke-[2px] fill-transparent"
                    />
                  </svg>

                  {/* Hover Animation */}
                  <style jsx>{`
                    a:hover svg path {
                      animation: wobble 0.6s infinite;
                    }

                    @keyframes wobble {
                      0% {
                        d: path("M0 2.5 Q25 0 50 2.5 T100 2.5");
                      }
                      25% {
                        d: path("M0 2.5 Q25 5 50 2.5 T100 2.5");
                      }
                      50% {
                        d: path("M0 2.5 Q25 0 50 2.5 T100 2.5");
                      }
                      75% {
                        d: path("M0 2.5 Q25 -5 50 2.5 T100 2.5");
                      }
                      100% {
                        d: path("M0 2.5 Q25 0 50 2.5 T100 2.5");
                      }
                    }
                  `}</style>
                </Link>
              );
            })}
          </div>

          <div className="hidden md:flex items-center gap-4">
            <button
              onClick={toggleTheme}
              className="p-2 rounded-full bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-200 hover:bg-gray-300 dark:hover:bg-gray-600 transition-colors"
              aria-label="Toggle dark mode"
            >
              {isDark ? (
                <Sun className="w-5 h-5" />
              ) : (
                <Moon className="w-5 h-5" />
              )}
            </button>
            <Link
              href="#contact"
              className="bg-linear-to-r from-blue-600 to-purple-600 text-white px-6 py-2 rounded-full font-medium hover:shadow-lg transform hover:-translate-y-0.5 transition-all"
            >
              Get Quote
            </Link>
          </div>

          {/* Mobile Menu Button */}
          <div className="md:hidden flex items-center gap-2">
            <button
              onClick={toggleTheme}
              className="p-2 rounded-full bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-200 cursor-pointer"
              aria-label="Toggle dark mode"
            >
              {isDark ? (
                <Sun className="w-5 h-5" />
              ) : (
                <Moon className="w-5 h-5" />
              )}
            </button>
            <button onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}>
              {isMobileMenuOpen ? (
                <X
                  className={
                    isScrolled ? "text-gray-900 dark:text-white" : "text-white"
                  }
                />
              ) : (
                <Menu
                  className={
                    isScrolled ? "text-gray-900 dark:text-white" : "text-white"
                  }
                />
              )}
            </button>
          </div>
        </div>

        {/* Mobile Menu */}
        {isMobileMenuOpen && (
          <div className="md:hidden mt-4 bg-white dark:bg-gray-800 rounded-lg shadow-xl p-4">
            {navLinks.map((link) => (
              <Link
                key={link.name}
                href={link.href}
                className="block py-3 text-gray-700 dark:text-gray-200 hover:text-blue-600 dark:hover:text-blue-400 font-medium border-b border-gray-100 dark:border-gray-700 last:border-0"
                onClick={() => setIsMobileMenuOpen(false)}
              >
                {link.name}
              </Link>
            ))}
            <Link
              href="#contact"
              className="block mt-4 bg-linear-to-r from-blue-600 to-purple-600 text-white px-6 py-3 rounded-full font-medium text-center"
              onClick={() => setIsMobileMenuOpen(false)}
            >
              Get Quote
            </Link>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Header;
